import React from "react";
import {
  ActionBar,
  ActionForm,
  Carousel,
  Footer,
  HiBanner,
  SkillWork,
  WorkExperience,
} from "./components";
import profile from "./profile.json";

function App() {
  return (
    <div>
      <ActionBar />
      <HiBanner contact={profile.contact} data={profile.welcome} />
      <Carousel data={profile.projects} />
      <SkillWork data={profile.techStacks} />
      <WorkExperience data={profile.workExperiences} />
      <ActionForm data={profile.actionForm} />
      <Footer data={profile.footer} />
    </div>
  );
}

export default App;
