import styled from "@emotion/styled";
import React, { useState } from "react";
import { defaultData, IActionForm } from ".";
import { Popup } from "../Popup";
import * as S from "./styles";

interface Props {
  data?: IActionForm;
}

export function ActionForm(props: Props) {
  const { data = defaultData } = props;
  const [isOpen, setOpen] = useState(false);
  const [form, setForm] = useState<Record<string, any>>({});

  const onSubmit = (e: any) => {
    e.preventDefault();
    setOpen(true);

    const xmlhttp = new XMLHttpRequest();
    const url =
      process.env.REACT_APP_EMAIl_FUNCTION_URL ?? "http://localhost:1234";
    xmlhttp.open("POST", url);
    xmlhttp.setRequestHeader("Content-Type", "application/json;charset=UTF-8");
    xmlhttp.send(JSON.stringify(form));
  };

  return (
    <S.Wrapper id="about">
      <S.Card>
        <S.Title>{data.title}</S.Title>
        <S.SubTitle>{data.subTitle}</S.SubTitle>
        <form onSubmit={onSubmit}>
          {data.fields.map((field) => (
            <S.Field key={field.label}>
              <input
                placeholder={field.label}
                value={form[field.key] ?? ""}
                onChange={(e) =>
                  setForm((form) => ({ ...form, [field.key]: e.target.value }))
                }
              />
            </S.Field>
          ))}
          <S.ActionButton type="submit">Send</S.ActionButton>
        </form>
        <Popup isOpen={isOpen} onClose={() => setOpen(false)}>
          <PopupWrapper>
            <Icon>
              <div className="check-icon">
                <span className="icon-line line-tip"></span>
                <span className="icon-line line-long"></span>
                <div className="icon-circle"></div>
                <div className="icon-fix"></div>
              </div>
            </Icon>
            <Message>Sent</Message>
            <Button onClick={() => setOpen(false)}>Close</Button>
          </PopupWrapper>
        </Popup>
      </S.Card>
    </S.Wrapper>
  );
}

const PopupWrapper = styled.div`
  text-align: center;
`;

const Message = styled.h4``;

const Button = styled.div`
  border: 1px solid ${({ theme }) => theme.body1};
  color: ${({ theme }) => theme.body1};
  font-size: ${({ theme }) => theme.caption};
  padding: 0.75rem 2rem;
  font-weight: bold;
  border-radius: 0.25rem;
  display: inline-block;
  margin-top: 1rem;
`;

const Icon = styled.div`
  width: 80px;
  height: 100px;
  margin: 0 auto;

  .check-icon {
    width: 80px;
    height: 80px;
    position: relative;
    border-radius: 50%;
    box-sizing: content-box;
    border: 4px solid #4caf50;

    &::before {
      top: 3px;
      left: -2px;
      width: 30px;
      transform-origin: 100% 50%;
      border-radius: 100px 0 0 100px;
    }

    &::after {
      top: 0;
      left: 30px;
      width: 60px;
      transform-origin: 0 50%;
      border-radius: 0 100px 100px 0;
      animation: rotate-circle 4.25s ease-in;
    }

    &::before,
    &::after {
      content: "";
      height: 100px;
      position: absolute;
      background: transparent;
      transform: rotate(-45deg);
    }

    .icon-line {
      height: 5px;
      background-color: #4caf50;
      display: block;
      border-radius: 2px;
      position: absolute;
      z-index: 10;

      &.line-tip {
        top: 46px;
        left: 14px;
        width: 25px;
        transform: rotate(45deg);
        animation: icon-line-tip 0.75s;
      }

      &.line-long {
        top: 38px;
        right: 8px;
        width: 47px;
        transform: rotate(-45deg);
        animation: icon-line-long 0.75s;
      }
    }

    .icon-circle {
      top: -4px;
      left: -4px;
      z-index: 10;
      width: 80px;
      height: 80px;
      border-radius: 50%;
      position: absolute;
      box-sizing: content-box;
      border: 4px solid rgba(76, 175, 80, 0.5);
    }

    .icon-fix {
      top: 8px;
      width: 5px;
      left: 26px;
      z-index: 1;
      height: 85px;
      position: absolute;
      transform: rotate(-45deg);
      background-color: transparent;
    }
  }

  @keyframes rotate-circle {
    0% {
      transform: rotate(-45deg);
    }
    5% {
      transform: rotate(-45deg);
    }
    12% {
      transform: rotate(-405deg);
    }
    100% {
      transform: rotate(-405deg);
    }
  }

  @keyframes icon-line-tip {
    0% {
      width: 0;
      left: 1px;
      top: 19px;
    }
    54% {
      width: 0;
      left: 1px;
      top: 19px;
    }
    70% {
      width: 50px;
      left: -8px;
      top: 37px;
    }
    84% {
      width: 17px;
      left: 21px;
      top: 48px;
    }
    100% {
      width: 25px;
      left: 14px;
      top: 45px;
    }
  }

  @keyframes icon-line-long {
    0% {
      width: 0;
      right: 46px;
      top: 54px;
    }
    65% {
      width: 0;
      right: 46px;
      top: 54px;
    }
    84% {
      width: 55px;
      right: 0px;
      top: 35px;
    }
    100% {
      width: 47px;
      right: 8px;
      top: 38px;
    }
  }
`;
