export enum EComponentType {
  ActionForm = "ActionForm",
  Carousel = "Carousel",
  Default = "Default",
  Footer = "Footer",
  HiBanner = "HiBanner",
  Popup = "Popup",
  SkillWork = "SkillWork",
  WorkExperience = "WorkExperience",
}

export enum EDefinitionType {
  String = "String",
  Number = "Number",
  Object = "Object",
  Array = "Array",
  Boolean = "Boolean",
  File = "File",
  Files = "Files",
  Select = "Select",
}

export interface Definition {
  name: string;
  defaultValue: string;
  type: EDefinitionType;
}

export interface Block {
  _id: string;
  component: EComponentType;
  data: any;
  definitions: Definition[];
}
