import React from "react";
import { defaultData, ISkillWork } from ".";
import * as S from "./styles";

interface Props {
  data?: ISkillWork;
}

export function SkillWork(props: Props) {
  const { data = defaultData } = props;

  return (
    <S.Wrapper>
      <h2>Achievements</h2>
      <S.SkillList>
        {data.records.map((skill, i) => (
          <S.SkillItemWrapper key={i}>
            <S.SkillItem>
              <div className="name">{skill.title}</div>
              <div className="shortName">{skill.subTitle}</div>
              <div className="detail">{skill.detail}</div>
            </S.SkillItem>
          </S.SkillItemWrapper>
        ))}
      </S.SkillList>
    </S.Wrapper>
  );
}
