import { motion } from "framer-motion";
import React, { useMemo, useState } from "react";
import { defaultData, ICarousel } from ".";
import * as S from "./styles";

interface Props {
  data?: ICarousel;
}

const OFFSET = 15;
const HEIGHT = 200;

const getItemStyle = (
  length: number,
  offsetIndex: number,
  isExpanded: boolean
) => {
  const style = {
    zIndex: length - offsetIndex,
    transform: isExpanded ? undefined : `scale(${1 - offsetIndex / 50})`,
    height: HEIGHT,
  };

  return style;
};

const getAnimated = (i: number, isExpanded: boolean) => {
  return {
    top: isExpanded ? i * HEIGHT + i * OFFSET : i * OFFSET,
  };
};

const getParentStyle = () => {
  return {
    height: HEIGHT + 60,
  };
};

const getParentStyleExpanded = (length: number) => {
  return {
    height: length * (HEIGHT + 15) + 60,
  };
};

const MotionWrapper = motion(S.Wrapper);
const MotionRecords = motion(S.Records);
const MotionRecord = motion(S.Record);

export function Carousel(props: Props) {
  const { data = defaultData } = props;
  const [isExpanded, setIsExpanded] = useState(false);
  const [activeIndex, setActiveIndex] = useState(0);
  const indexedRecords = useMemo(
    () => data.records.map((record, index) => ({ ...record, id: index })),
    [data.records]
  );

  const records = isExpanded
    ? indexedRecords
    : indexedRecords.slice(
        Math.max(activeIndex - 2, 0),
        Math.min(
          activeIndex < 2 ? activeIndex + 4 : activeIndex + 2,
          indexedRecords.length
        )
      );

  const scrollToView = () => {
    const element = document.querySelector(".skill-top");
    element?.scrollIntoView({
      behavior: "smooth",
      block: "center",
    });
  };

  const onClickItem = (i: number) => {
    if (isExpanded) {
      scrollToView();
      setActiveIndex(i);
    }
    setIsExpanded((state) => !state);
  };

  return (
    <MotionWrapper
      animate={
        isExpanded
          ? getParentStyleExpanded(indexedRecords.length)
          : getParentStyle()
      }
      id="skills"
    >
      <div className="skill-top"></div>
      <MotionRecords
        whileHover={{ scale: 1.02 }}
        whileTap={{ scale: 0.98, transition: { duration: 0.02 } }}
      >
        {records.map((record, i) => (
          <MotionRecord
            key={record.id}
            onMouseDown={() => onClickItem(record.id)}
            className={`${isExpanded ? "active" : ""}`}
            style={getItemStyle(
              records.length,
              Math.abs(activeIndex - record.id),
              isExpanded
            )}
            animate={getAnimated(i, isExpanded)}
          >
            <div>
              <S.Title>{record.title}</S.Title>
              {/* <S.SubTitle>{record.subTitle}</S.SubTitle> */}
              <S.Detail>{record.detail}</S.Detail>
            </div>
            <div>{record.thumbUrl}</div>
          </MotionRecord>
        ))}
      </MotionRecords>
    </MotionWrapper>
  );
}
