import { motion } from "framer-motion";
import React, { useRef } from "react";
import { IPopup } from ".";
import * as S from "./styles";

interface Props {
  data?: IPopup;
  isOpen: boolean;
  children?: React.ReactNode;
  onClose: () => void;
  layoutId?: string;
}

const MotionContent = motion(S.Content);

export function Popup(props: Props) {
  const { isOpen, layoutId, children, onClose } = props;
  const contentRef = useRef<HTMLDivElement | null>(null);

  const onClickBackdrop = (e: any) => {
    if (
      contentRef.current === e.target ||
      contentRef.current?.contains(e.target)
    )
      return;
    onClose();
  };

  return isOpen ? (
    <S.Wrapper onClick={onClickBackdrop}>
      <MotionContent layoutId={layoutId} ref={contentRef}>
        {children}
      </MotionContent>
    </S.Wrapper>
  ) : null;
}
