import {
  css,
  Global,
  ThemeProvider as ThemeProviderRoot,
} from "@emotion/react";
import React, { useMemo, useState } from "react";

const size = {
  mini: "0.75rem",
  caption: "0.85rem",
  paragraph: "1rem",
  heading5: "1.25rem",
  heading4: "1.5rem;",
  heading3: "2rem",
  heading2: "2.5rem;",
  heading1: "3rem",
};

export const darkTheme = {
  ...size,
  bgText: "#ffffff",
  bg: "#1c1f23",
  body1: "#fafafa",
  body2: "#cacaca",
  logo: "#ffffff",
  card: "#2b2f33",
  border: "#31383f",

  // COLORS
  primary: "#00acff",
  primaryHover: "#27b1f7",
  primaryActive: "#119de4",

  black: "#000000",
  black1: "#1c1f23",
  black2: "#2b2f33",
  black3: "#31383f",
  white: "#151111",
  white1: "#fafafa",
  white3: "#cacaca",
  orange: "#fe4902",
  green: "#4cc93f",

  whiteOpacity1: "#ffffff11",
  whiteOpacity2: "#ffffff22",
  whiteOpacity3: "#ffffff33",
  whiteOpacity5: "#ffffff55",

  boxShadow: "rgba(0, 0, 0, 0.1)",
};

export const lightTheme = {
  ...size,
  bgText: "#1c1f23",
  bg: "#fefefe",
  body1: "#213547",
  body2: "#31383f",
  logo: "#ff937d",
  card: "#F5F5F5",
  border: "#cacaca",

  // COLORS
  primary: "#00acff",
  primaryHover: "#27b1f7",
  primaryActive: "#119de4",

  black: "#000000",
  black1: "#1c1f23",
  black2: "#2b2f33",
  black3: "#31383f",
  white: "#ffffff",
  white1: "#fafafa",
  white3: "#cacaca",
  orange: "#fe4902",
  green: "#4cc93f",

  whiteOpacity1: "#ffffff11",
  whiteOpacity2: "#ffffff22",
  whiteOpacity3: "#ffffff33",
  whiteOpacity5: "#ffffff55",

  boxShadow: "rgba(0, 0, 0, 0.05)",
};

const globalCss = (theme: any) => css`
  * {
    box-sizing: border-box;
  }

  body {
    margin: 0;
    font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto",
      "Oxygen", "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans",
      "Helvetica Neue", sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
  }

  code {
    font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
      monospace;
  }

  .root {
    color: ${theme.bgText};
    background: ${theme.bg};
    transition: background-color 200ms cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }
`;

export const level = `
  transition: box-shadow cubic-bezier(0.25, 0.46, 0.45, 0.94) 300ms;
`;

export const boxShadowNormal = `
  ${level}
  box-shadow: rgba(0, 0, 0, 0.1) 0px 4px 12px;
`;

export const boxShadowDark = ({ theme }: any) => css`
  ${level}
  box-shadow: ${theme.boxShadow} 0px 10px 15px;
`;

export const boxShadowSurround = `
  ${level}
  box-shadow: rgba(0, 0, 0, 0.16) 0px 10px 50px;
`;

export const ThemeContext = React.createContext({
  isDarkMode: true,
  toggle: () => {},
});

export const ThemeProvider = ({ children }: { children?: JSX.Element }) => {
  const [isDarkMode, setIsDarkMode] = useState(
    Boolean(Number(localStorage.getItem("darkMode") ?? 1))
  );

  const value = useMemo(
    () => ({
      isDarkMode,
      toggle: () => {
        setIsDarkMode((state) => !state);
        localStorage.setItem("darkMode", String(!isDarkMode ? 1 : 0));
      },
    }),
    [isDarkMode]
  );

  return (
    <ThemeContext.Provider value={value}>
      <ThemeProviderRoot theme={isDarkMode ? darkTheme : lightTheme}>
        <Global styles={globalCss} />
        <div className="root">{children}</div>
      </ThemeProviderRoot>
    </ThemeContext.Provider>
  );
};
