import { AnimatePresence } from "framer-motion";
import React, { useState } from "react";
import { defaultData, IExperience, IWorkExperience } from ".";
import { Popup } from "../Popup";
import * as S from "./styles";
import { WorkExperienceItem } from "./WorkExperienceItem";

interface Props {
  data?: IWorkExperience;
}

const ITEM_WIDTH = 240;
const ITEM_TOP_GAP = 100;
const COLUMN_WIDTH_SIZE = 200;

const YEAR_NOW = new Date().getFullYear();
const MIN_RANGE = 10;

const getDiffInYear = (from: Date, to: Date) => {
  const diff = Math.abs(to.getTime() - from.getTime());
  return diff / (1000 * 60 * 60 * 24 * 360);
};

const getWidth = (from: Date, to: Date | null) => {
  if (!to) return 1;
  return Math.max(getDiffInYear(from, to), 1);
};

const getLeft = (from: Date) => {
  return Math.max(getDiffInYear(from, new Date()), 0);
};

const getPositionStyle = ({ from, to }: IExperience, i: number) => {
  return {
    top: (i % 3) * ITEM_TOP_GAP,
    left: (getLeft(new Date(from)) + 0.5) * COLUMN_WIDTH_SIZE,
    width: ITEM_WIDTH * getWidth(new Date(from), to ? new Date(to) : null),
  };
};

export function WorkExperience(props: Props) {
  const { data = defaultData } = props;
  const [isOpen, setOpenPopup] = useState(false);
  const [openedExperience, setOpenExperience] = useState<IExperience>();
  const years = data.experience
    .filter((experience) => experience.to)
    .map((experience) => new Date(experience.to).getFullYear());

  const range = Math.max(MIN_RANGE, YEAR_NOW - Math.min(...years));
  const yearLists = Array(range)
    .fill(undefined)
    .map((_, i) => YEAR_NOW - i);

  const list = data.experience.map((experience, i) => ({
    ...experience,
    style: getPositionStyle(experience, i),
  }));

  const onViewDetail = (experience: IExperience) => {
    setOpenExperience(experience);
    setOpenPopup(true);
  };

  return (
    <S.Wrapper>
      <h2>{data.title}</h2>
      <S.Timeline>
        <S.Years>
          {yearLists.map((year) => (
            <S.Year key={year} style={{ width: `${COLUMN_WIDTH_SIZE}px` }}>
              {year}
            </S.Year>
          ))}
        </S.Years>

        <S.Experiences>
          {list.map((experience, i) => (
            <S.Experience key={i} style={experience.style}>
              <WorkExperienceItem
                experience={experience}
                onViewDetail={onViewDetail}
              />
            </S.Experience>
          ))}
        </S.Experiences>

        <AnimatePresence>
          <Popup
            layoutId={openedExperience?.id}
            isOpen={isOpen}
            onClose={() => setOpenPopup(false)}
          >
            <S.Ulist>
              {openedExperience?.outlines.map((outline, i) => (
                <li key={i}>{outline}</li>
              ))}
            </S.Ulist>
          </Popup>
        </AnimatePresence>
      </S.Timeline>
    </S.Wrapper>
  );
}
