interface Props {
  data?: IHiBanner;
  contact?: IContact;
}

export function HiBanner(props: Props) {
  const { data, contact } = props;

  const scrollToAbout = () => {
    const element = document.querySelector("#about");
    element?.scrollIntoView({
      behavior: "smooth",
      block: "end",
      inline: "nearest",
    });
  };

  return (
    <S.Wrapper>
      {/* <img src={BannerImg} alt="background" /> */}
      {/* <S.Wavy></S.Wavy> */}

      <S.Content>
        <S.Greeting>
          {getAttributeOrDefault("greeting", data, defaultData)}
        </S.Greeting>
        <MotionName
          animate={{
            skewX: "-10deg",
          }}
          transition={{ delay: 0.5, type: "spring", stiffness: 500 }}
        >
          {getAttributeOrDefault("name", data, defaultData)}
        </MotionName>
        <S.Title>{getAttributeOrDefault("title", data, defaultData)}</S.Title>
        <S.IconActions>
          <a href={contact?.email} target="_blank">
            <Icon size={1.2} path={mdiAt} />
          </a>
          <a href={contact?.github} target="_blank">
            <Icon size={1.2} path={mdiGithub} />
          </a>
          <a href={contact?.phone} target="_blank">
            <Icon size={1.2} path={mdiPhone} />
          </a>
        </S.IconActions>
        <S.Detail>
          {getAttributeOrDefault("detail", data, defaultData)}
        </S.Detail>
        <div>
          <S.Link onClick={scrollToAbout}>
            {getAttributeOrDefault("contactLabel", data, defaultData)}
          </S.Link>
        </div>
      </S.Content>
    </S.Wrapper>
  );
}
