import React from "react";
import * as S from "./styles";

interface IFooter {
  data?: {
    copyright: string;
  };
}

export function Footer({ data }: IFooter) {
  return (
    <S.Footer>
      <small>
        {data?.copyright ?? "&copy; Copyright 2018, Example Corporation"}
      </small>
    </S.Footer>
  );
}
