import { motion } from "framer-motion";
import React from "react";
import { IExperience } from "..";
import * as S from "./styles";

interface Props {
  experience: IExperience;
  onViewDetail: (experience: IExperience) => void;
}

const MotionItem = motion(S.ExperienceItem);

export function WorkExperienceItem(props: Props) {
  const { experience, onViewDetail } = props;

  return (
    <MotionItem
      layoutId={experience.id}
      onClick={() => onViewDetail(experience)}
    >
      <S.Title>{experience.title}</S.Title>
      <S.Date>
        {experience.from && formatDate(experience.from)}
        {experience.to && " - "}
        {experience.to && formatDate(experience.to)}
      </S.Date>
    </MotionItem>
  );
}

const formatDate = (dateString: string) => {
  const date = new Date(dateString);
  return date.getMonth() + 1 + "/" + date.getFullYear();
};
